.zebra {
    font-family: 'Roboto', sans-serif;
    border-collapse: collapse;
    width: 100%;
    th, td {
      padding: 0.25rem;
      text-align: left;
      border: 1px solid #ccc;
    }
    tbody tr:nth-child(odd) {
      background: #eee;
    }
  }

  h2{
    font-family: 'Roboto', sans-serif;

  }

  